<template>
    <div>
        <loader :status="is_saving" />


            <b-row class="m-0">
                <b-col class="p-3 bg-light d-flex align-items-center" cols="6">
                    <div>
                        <h6 class="mb-0 text-uppercase"><strong>{{voter.name}} ({{voter.precint.name}})</strong> </h6>
                        <small class="text-muted text-uppercase">{{voter.member_type.name}}</small>
                    </div>
                </b-col>
                <b-col class="p-3 bg-light d-flex align-items-center">
                    <div>
                        <h6 class="mb-0"><strong>{{voter.total_child}}</strong> </h6>
                        <small class="text-muted text-uppercase">TOTAL MEMBERS</small>
                    </div>
                </b-col>
                <b-col class="p-3 bg-light d-flex align-items-center">
                    <div>
                        <h6 class="mb-0"><strong>{{voter.barangay.name}}</strong> </h6>
                        <small class="text-muted text-uppercase">BARANGAY</small>
                    </div>
                </b-col>
            </b-row>
            <b-row>
                <b-col class="border" cols="6">
                    <span><strong>NAME</strong></span>
                </b-col>
                <b-col class="border">
                    <span><strong>TYPE</strong></span>
                </b-col>
                <b-col class="border">
                    <span><strong>PRECINT NO.</strong></span>
                </b-col>
            </b-row>
            <NestedVoterList v-for="(item, index) in voter.children" :key="item.id" :node="item" :numbering="index+1" />
    </div>
</template>
<script>
    import SwalMixin from '@/views/mixins/Swal.js'
    import Loader from '@/views/helpers/LoaderComponent.vue';
    import NumberFormatComponent from '@/views/helpers/NumberFormatComponent.vue';
    import { assets_path } from "@/config";
    import {bus} from '@/main.js';

    import NestedVoterList from '@/views/print/NestedVoterList.vue';
    export default {
        mixins:[SwalMixin],
        components: {Loader,NumberFormatComponent,NestedVoterList},
        props:{
            id: {
                type: Number,
                default: -1
            },
        },
        data() {
            return {
                assets_path: {},
                is_saving:false,
                voter:{},
            }
        },
        computed:{
        },
        mounted() {
            this.setupPrintHeader();
        },
        created(){
            this.id=this.$route.params.id;
            this.assets_path = assets_path;
            this.get();
        },
        methods: {
            get(){
                this.is_saving=true;
                this.membersTableKey++;
                this.axios.get('voters/get',{params:{id:this.id}}).then(response => {
                    //console.log(result)
                    this.voter = response.data;
                    this.is_saving=false;
                }).catch(error => console.log(error));
            },
            setupPrintHeader() {
                window.onbeforeprint = () => {
                    // Move header after the first page
                    setTimeout(() => {
                        // let header = this.$refs.printHeader;
                        // if (header) {
                        //     header.style.display = "block"; // Show header
                        // }
                        // let content = this.$refs.printContent;
                        // if (content) {
                        //     content.style.marginTop = "0px"; // Adds margin only from the second page onward
                        // }
                    }, 100);
                };
            }
        }
    }
</script>
